import Image from './Image'; // Charge Image
import galerieTemplate from './templates/galerie'; // Charge le template de la galerie

export default class Galerie { // D√©finit la propri√©t√© √©l√©ment (el)
  constructor(data) { // data r√©cup√©r√©es de l'instanciation new Galerie
      this.el = document.querySelector(data.el);
      this.listEl;
      this.images = []; // On met les donn√©es charg√©e ci dessous dans ce tableau vide.
      this._loadImages(data.images); // On charge les donn√©es des images pour hydrater this.images
      this.template = galerieTemplate; // On charge le code html du template de la Galerie (via import ci dessus)
      this.render();
      this.renderImgSlide();
      this.renderImgMenu();
  }

  // METHODE loadImages - Chargement des images sous formes d'objets de type Image dans this.images
  // But => Parcour tous les images pour en faire des objet de type Image
  _loadImages(images) {
      for (let image of images) {
          this.images.push(new Image({
              parent: this,
              image
          })); //dans (image));, on envois un tableau de type json avec 4 propri√©t√©s (id, src, alt, content)
      }
  }

  // Rendu de la Galerie
  render() {
      this.el.innerHTML = this.template; // On met le template sur l'element sur lequel on a greffer l'application
      // L'√©l√©ment .image-list et .image-menu existe pour le naviguateur
  }

  renderImgSlide() {
      this.listEl = this.el.querySelector('.image-list');
      // Rendu des images - On demande √† chacun des images de faire un render, donc de s'affciher
      for (let image of this.images) {
          image.imageRender();
      }
  }

  renderImgMenu() {
      this.listEl = this.el.querySelector('.image-menu');
      for (let image of this.images) {
          image.menuRender();
      }
  }

}
